.clear {
    clear: both;
  }
  
  /*********************
    GENERAL
    *********************/
  
  body {
    margin: 0;
    left: 0;
    background-color: #EEEEEE;
    font-family: sans-serif;
    -webkit-font-smoothing: antialiased;
    text-rendering: optimizeLegibility;
  }
  
  /*********************
    HEADER
    *********************/
  
  .header {
    position: fixed;
    background-color: white;
    width: 100%;
    height: 61px;
    padding: 0;
    z-index: 100;
  }
  
  .docsLogo {
    margin: -20px 0 0 -8px;
    /* position: absolute; */
    float: left;
    z-index: 3;
  }
  
  #untitled {
    font-size: 1.1em;
    color: grey;
    font-weight: 400;
    margin-left: 60px;
    margin-top: -6px;
    position: absolute;
    padding: 0 3px 0;
  }
  
  #untitled:hover {
    background-color: #eee;
  }
  
  .whiteBar ul {
    list-style: none;
    margin-left: 20px;
  }
  
  li {
    display: inline;
    position: relative;
    margin: 0px;
    float: left;
    top: 22px;
    font-size: 0.85em;
    z-index: 2;
    padding: 3px 5px;
    border-radius: 0.15em;
  }
  
  li:hover {
    background-color: #eee;
  }
  
  .rightSide {
    position: relative;
    float: right;
    margin: -45px 2% 3px 0;
    margin-top: -5px;
    font-size: 0.7em;
  }
  
  #emailLogin {
    color: grey;
    padding: 0;
    margin-left: -10px;
    opacity: 0.8;
  }
  
  #emailLogin:hover {
    color: black;
    opacity: 0.8;
  }
  
  #emailcaret {
    position: relative;
    float: right;
    margin: -1px 0 0 3px;
    padding: 0;
    opacity: 0.7;
  }
  
  #emailcaret:hover {
    opacity: 1.0;
  }
  
  .extraButtons {
    position: absolute;
    width: 100px;
    left: -160px;
    list-style: none;
    text-align: center;
    padding: 0;
    margin: -17px 0 0px auto;
  }
  
  .extraButtons li {
    position: relative;
    float: right;
    display: inline;
    padding: 7px;
    background-color: white;
    margin: 0 -55px 5px;
    color: black;
    font-size: 1em;
    font-weight: bold;
    border: 1px solid #c9c9c9;
    border-radius: 0.25em;
    box-shadow: 0 0 1px #C7C7C7;
    background-color: #F6F6F6;
  }
  
  .extraButtons li:hover {
    box-shadow: 0 0 1px #737373;
  }
  
  .extraButtons li:nth-child(2) {
    background-color: #377CEA;
    right: -75px;
    width: 50px;
    color: white;
  }
  
  .extraButtons li:nth-child(2):hover {
    background-color: #4285F4;
  }
  
  /*********************
    PAGE
    *********************/
  
  .page {
    position: absolute;
    width: 820px;
    height: 1100px;
    padding: 20px;
    left: 49.5%;
    margin: 130px 0 50px -410px;
    background-color: white;
    box-shadow: 0 0 5px grey;
    font-family: 'Open Sans', sans-serif;
  }
  
  #cursor {
    margin: 0px 0 0 30px;
  }
  
  .page2 {
    position: absolute;
    padding: 20px;
    width: 820px;
    height: 1100px;
    left: 49.5%;
    margin: 1250px 0 50px -410px;
    background-color: white;
    box-shadow: 0 0 5px grey;
  }
  
  #paragraph {
    width: 700px;
    text-align: justify;
    margin-left: auto;
    margin-right: auto;
    margin-top: 80px;
  }
  
  #headline {
    font-size: 1.5em;
    color: #007db6;
    font-weight: bold;
  }
  
  .h2 {
    font-weight: 600;
    font-size: 1.0em;
    color: #3b3d47;
  }
  
  a {
    color: blue;
  }
  
  .blueBold {
    color: #007db6;
  }
  
  /*********************
    CONTENT
    *********************/
  
  #screen1 {
    width: 300px;
    position: relative;
    float: left;
    margin-left: 45px;
  }
  
