  .toolBar {
    width: 100%;
    height: 30px;
    background-color: #F0F0F0;
    margin-top: 27px;
    border-bottom: 1px solid #dbdbdb;
    padding-top: 5px;
  }
  
  .toolBag {
    /* background-color: red; */
